.070 114.714 C 62.971 111.510,51.496 103.200,49.171 103.200 C 48.464 103.200,47.325 102.639,46.640 101.954 C 45.954 101.269,44.923 100.888,44.349 101.109 C 43.774 101.330,43.173 101.119,43.013 100.640 C 42.811 100.034,42.160 100.072,40.865 100.765 C 39.682 101.398,39.204 101.440,39.551 100.880 C 40.179 99.863,38.557 99.788,36.800 100.752 "></path></g></svg>');
}
